import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Live = (resolve) => {
    import('views/live/live').then((module) => {
      resolve(module)
    })
}

const Fosterage = (resolve) => {
    import('views/fosterage/fosterage').then((module) => {
      resolve(module)
    })
}

const UserCenter = (resolve) => {
    import('views/user-center/user-center').then((module) => {
      resolve(module)
    })
}

const Room = (resolve) => {
    import('views/room/room').then((module) => {
      resolve(module)
    })
}

const SellerList = (resolve) => {
  import('views/seller-list/seller-list').then((module) => {
    resolve(module)
  })
}

const Seller = (resolve) => {
  import('views/seller/seller').then((module) => {
    resolve(module)
  })
}

const Comments = (resolve) => {
  import('views/comments/comments').then((module) => {
    resolve(module)
  })
}

export default new Router({
  routes: [{
    path: '/',
    redirect: '/live'
  },
  {
    path: '/live',
    component: Live
  },
  {
    path: '/room',
    component: Room
  },
  {
    path: '/fosterage',
    component: Fosterage
  },
  {
    path: '/user',
    component: UserCenter
  },
  {
    path: '/seller-list',
    component: SellerList
  },
  {
    path: '/seller',
    component: Seller
  },
  {
    path: '/comments',
    component: Comments
  }
  ]
})
