import storage from 'good-storage'

const USER_MOBILE_KEY = '__user_mobile__'
const USER_TOKEN_KEY = '__user_token__'
const USER_NAME_KEY = '__user_name__'

const state = {
  login_mobile: '',
  login_token: '',
  login_name: '',
  temp_mobile: '',
  temp_token: '',
  temp_name: ''
}

const getters = {
  currentUser: state => {
    return {
      mobile: state.login_mobile,
      token: state.login_token,
      name: state.login_name
    }
  }
}

const mutations = {
  updateData (state, payload) {
    switch (payload.name) {
      case 'mobile':
        state.temp_mobile = payload.value
        break
      case 'token':
        state.temp_token = payload.value
        break
      case 'name':
        state.temp_name = payload.name
        break
      default:
        console.log('Error:Dont directly mutate Vuex store')
    }
  },
  getLocalUser (state) {
    state.login_mobile = storage.get(USER_MOBILE_KEY, '')
  },
  setUser (state, payload) {
    state.login_mobile = payload.mobile
    state.login_token = payload.token
    state.login_name = payload.name
  },
  logout (state) {
    storage.remove(USER_MOBILE_KEY)
    storage.remove(USER_TOKEN_KEY)
    storage.remove(USER_NAME_KEY)
    state.login_mobile = ''
    state.login_token = ''
    state.login_name = ''
  }
}

const actions = {

  login ({ commit }, payload) {

  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
